.movie{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  color: white;
}

.movie__title{
  font-size: 20px;
  margin: 10px 0px;
  cursor: pointer;
}

.title{
  color: inherit;
  text-decoration: none;
}

.movie__cover{
  border-radius: 20px;
}